const NEW_MESSAGE = 'NEW-MESSAGE';


let initialState = {
    messages: [
        {id: 1, text: 'Hi'},
        {id: 2, text: 'How are you'}
    ],
    dialogs: [
        {id: 1, name: 'Andrey'},
        {id: 2, name: 'Sasha'},
        {id: 3, name: 'Misha'},
        {id: 4, name: 'Valera'},
        {id: 5, name: 'Maria'}
    ]
};


const dialogReducer = (state = initialState, action) => {
    switch ( action.type ) {
        case NEW_MESSAGE: {
            return {
                ...state,
                messages: [...state.messages, { id: 1, text: action.text}],
                messageText:''
            }
        }
        default:
            return state;
    }
};

export const addNewMessage = (text) => ({type: NEW_MESSAGE,text});

export default dialogReducer;