import React from 'react'
import {connect} from "react-redux";
import Profile from "./Profile";
import {profileLoader, profileStatusLoader, profileStatusUpdate} from "../../redux/profile-reducer";
import {withRouter} from "react-router-dom";
import LoadingSpinner from "../common/LoadingSpinner/LoadingSpinner";
import {compose} from "redux";




class ProfileContainer extends React.Component {

    componentDidMount = () => {
        let userId = this.props.match.params.userId;
        if (!userId) userId = this.props.userId;
        this.props.profileLoader(userId);
        this.props.profileStatusLoader(userId);
    };

    render = () => {
        if (this.props.isFetching) {
            return <LoadingSpinner/>
        }
        return (
            <Profile {...this.props}/>
        )
    }
}

let mapStateToProps = (state) => {
    return {
        profile:state.profilePage.currentProfile,
        isFetching: state.profilePage.isFetching,
        profileStatus: state.profilePage.profileStatus,
        userId: state.auth.id
    }
};

export default compose(
    connect(mapStateToProps,{profileLoader,profileStatusLoader,profileStatusUpdate}),
    withRouter)(ProfileContainer)
