import React,{useState,useEffect} from 'react'


function ProfileStatusWithHook(props) {
    let [editMode,setEditMode] = useState(false);
    let [status,setStatus] = useState(props.profileStatus);


    useEffect(() => {

        setStatus(props.profileStatus)
    },[props.profileStatus]);

    const activateEditMode = () => {
        setEditMode(true)
    };
    const deactivateEditMode = () => {
        setEditMode(false);
        props.profileStatusUpdate(status)
    };
    const changeLocalStatus = (e) => {
        setStatus(e.currentTarget.value)
    };





        return (
            <div>
                {!editMode &&
                <div><span onClick={activateEditMode}>{props.profileStatus || 'Введите статус...'}</span></div>}

                {editMode &&
                <div><input autoFocus={true} value={status} onChange={changeLocalStatus} onBlur={deactivateEditMode}  type="text"/></div>}
            </div>
        )
}
export default ProfileStatusWithHook;