import React from 'react'
import s from './MessageList.module.css'
import Message from "./Message/Message";
import {} from "../../../redux/dialogs-reducer";
import NewMessageForm from "./Message/NewMessageForm";


function MessageList(props) {


    let messageElem = props.messageState.messages
        .map(elem => <Message text={elem.text}/>);


    let onSubmit = (text) => {
        props.addNewMessage(text.messageText)
    };
    return (
        <div>
            <div className={s.messageList}>
                {messageElem}
            </div>
            <div className={s.newMassage}>
                <NewMessageForm onSubmit={onSubmit}/>
            </div>
        </div>
    )
}

export default MessageList