import React from 'react';
import s from './ProfileInfo.module.css'
import defaultPhoto from '../../../assets/images/images.png'
import {ProfileStatus} from "../ProfileStatus";
import ProfileStatusWithHook from "../ProfileStatusWithHook";


function ProfileInfo(props) {

    return (
        <div className={s.profileTop}>
            <div className={s.photoItem}>

                <img
                    src={props.photos.large ? props.photos.large : defaultPhoto}
                    className={s.photo} alt=""/>
            </div>
            <div className={s.info}>
                <div className={s.nameWrapper}>
                    <h1 className={s.name}>{props.fullName ? props.fullName : `Missing name`}</h1>
                    <ProfileStatusWithHook profileStatus={props.profileStatus} profileStatusUpdate={props.profileStatusUpdate}/>
                </div>
                <div className={s.line}></div>
                <div className={s.userInfo}>
                    <ul>

                        <li>{props.aboutMe ? `About me: ${props.aboutMe}` : null}</li>
                        <li>{props.lookingForAJob ? `Looking for a job` : null}</li>
                        <li>{props.lookingForAJob && props.lookingForAJobDescription ?
                            `Description: ${props.lookingForAJobDescription}` : null}</li>
                    </ul>
                    <ul>
                        <li></li>
                        <li>{props.contacts.facebook ? `FaceBook: ${props.contacts.facebook}` : null}</li>
                        <li>{props.contacts.website ? `Website: ${props.contacts.website}` : null}</li>
                        <li>{props.contacts.vk ? `Vk: ${props.contacts.vk}` : null}</li>
                        <li>{props.contacts.twitter ? `Twitter: ${props.contacts.twitter}` : null}</li>
                        <li>{props.contacts.instagram ? `Instagram: ${props.contacts.instagram}` : null}</li>
                        <li>{props.contacts.facebook ? `FaceBook: ${props.contacts.facebook}` : null}</li>
                        <li>{props.contacts.github ? `Github: ${props.contacts.github}` : null}</li>
                        <li>{props.contacts.mainLink ? `MainLink: ${props.contacts.mainLink}` : null}</li>
                    </ul>
                </div>
            </div>
        </div>
    )
}

export default ProfileInfo