import React from 'react';
import s from './UserItem.module.css'
import userPhoto from '../../../assets/images/images.png'
import {NavLink} from "react-router-dom";





function UserItem(props) {

    return (
        <div className={s.wrapper}>
            <div className={s.imgDiv}>
                <NavLink to={`/profile/${props.id}`}>
                    <img src={props.photos.small != null ? props.photos.small : userPhoto} alt=""/>
                </NavLink>
            </div>
            <div className={s.buttonDiv}>
                {props.followed ?
                    <button disabled={props.followingProgress.some(id => id === props.id)}
                            onClick={() => props.unfollow(props.id)}>unsub</button> :
                    <button disabled={props.followingProgress.some(id => id === props.id)}
                            onClick={() => props.follow(props.id)}>subscribe</button>}
            </div>
            <div className={s.nameDiv}>{props.name}</div>
            <div className={s.statusDiv}>{props.status}</div>
            <div className={s.locationDiv}>
                <ul>
                    <li>Country: Ukraine</li>
                    <li>City: Alert</li>
                </ul>
            </div>
        </div>
    )
}

export default UserItem