import React from 'react'
import {connect} from "react-redux";
import {allUserCount, follow, followingInProgress, getUsers, unfollow,} from "../../redux/findUsers-reducer";
import FindUsers from "./FindUsers";
import {compose} from "redux";
import {withAuthRedirect} from "../../hoc/withAuthRedirect";
import {
    currentPageSelector, followingProgressSelector,
    getUsersSelector, isFetchingSelector,
    maxUsersOnPageSelector, portionSizeSelector,
    userCountSelector
} from "../../redux/findUsers-selectors";



class FindUsersContainer extends React.Component {

    componentDidMount = () => {
        this.props.getUsers(this.props.currentPage,this.props.maxUsersOnPage)

    };
    onPageChanged = (pageNumber) => {
        this.props.getUsers(pageNumber,this.props.maxUsersOnPage)
    };

    render = () => {
        return (
            <FindUsers {...this.props} onPageChanged={this.onPageChanged}/>
        )
    }
}

let mapStateToProps = (state) => {

    return {
        users: getUsersSelector(state),
        maxUsersOnPage: maxUsersOnPageSelector(state),
        userCount: userCountSelector(state),
        currentPage: currentPageSelector(state),
        isFetching: isFetchingSelector(state),
        followingProgress: followingProgressSelector(state),
        portionSize: portionSizeSelector(state)

    }
};


export default compose(
     connect(mapStateToProps, {unfollow, allUserCount,follow, followingInProgress,getUsers}),
     withAuthRedirect)(FindUsersContainer)




