import React from 'react'
import {Field, reduxForm} from "redux-form";
import {TextArea} from "../../common/inputs/textarea";
import {} from "../../../utils/validators/validators";
import {postMaxLength} from "../../../utils/validators/validators";




function NewPostFrom(props) {
    return (

        <form onSubmit={props.handleSubmit}>
            <div>
                <Field name={'postText'} component={TextArea} validate={postMaxLength}/>
            </div>
            <div>
                <button>New post</button>
            </div>
        </form>
    )
}

export default reduxForm({form:'newPostForm'})(NewPostFrom)