import React from 'react'
import {Field, reduxForm} from "redux-form";
import {connect} from "react-redux";
import {login} from "../../redux/auth-reducer";
import {InputArea} from "../common/inputs/textarea";
import {email, required} from "../../utils/validators/validators";
import {Redirect} from "react-router-dom";




function Login(props) {
    let onSubmit = (myForm) => {
        props.login(myForm)
    };

    if (props.isAuth) return <Redirect to='/profile'/>;

    return (
        <div>
            <h1>Login</h1>
            <LoginFormContainer onSubmit={onSubmit}/>
        </div>
    )
}

function LoginForm(props) {
    return (
        <form onSubmit={props.handleSubmit}>
            <div>
               <Field placeholder={"Email"} name={"email"} component={InputArea} validate={[required,email]}/>
            </div>
            <div>
                <Field placeholder={"Password"} name={"password"} component={InputArea} validate={[required]} />
            </div>
            <div>
                <Field name={"rememberMe"} component={"input"} type={"checkbox"}/>
            </div>
            {props.error && <div>{props.error}</div>}
            <button>Login</button>
        </form>
    )
}

let mapStateToProps = (state) => {
    return {
        isAuth: state.auth.isAuth
    }
};

let LoginFormContainer = reduxForm({ form: 'Login' })(LoginForm);

export default connect(mapStateToProps,{login})(Login);