import React from 'react'
import s from './Header.module.css'

function Header(props) {
    let logout = () => {
        props.logout()
    };
    return (
        <header className={s.header}>
            <div className={s.wrapper}>
            <img className={s.logo} src="http://logo.pizza/img/red-panda/red-panda-face.png"/>
            {props.isAuth ? <div><span>{props.login}</span><button onClick={logout}>Logout</button></div> :
            <div>
                <a href="" className={s.buttons}>Login</a>
                <a href="" className={s.buttons}>Register</a>
            </div>}
            </div>
        </header>
    )
}
export default Header