import {usersAPI} from "../API/API";

const SHOW_NEW_USERS = 'SHOW-NEW-USERS';
const USER_FOLLOW = 'USER-FOLLOW';
const USER_UNFOLLOWING = 'USER-UNFOLLOWING';
const ALL_USER_COUNT = 'ALL-USER-COUNT';
const CHANGE_CURRENT_PAGE = 'CHANGE-CURRENT-PAGE';
const TOGGLE_IS_FETCHING = 'TOGGLE-IS-FETCHING';
const FOLLOWING_IN_PROGRESS = 'FOLLOWING_IN_PROGRESS';

let initialState = {
    users: [],
    maxUsersOnPage: 6,
    userCount: 0,
    currentPage: 1,
    isFetching: false,
    followingInProgress: [],
    portionSize: 10

};


const FindUsersReducer = (state = initialState, action) => {
    switch ( action.type ) {
        case USER_FOLLOW:
            return {
                ...state,
                users: state.users.map(u => {
                    if (u.id === action.id) {
                        return {...u, followed: true}
                    }
                    return u
                })
            };
        case USER_UNFOLLOWING:
            return {
                ...state,
                users: state.users.map(u => {
                    if (u.id === action.id) {
                        return {...u, followed: false}
                    }
                    return u
                })
            };
        case SHOW_NEW_USERS:
            return {
                ...state, users: action.users
            };
        case ALL_USER_COUNT:
            return {
                ...state, userCount: action.userCount
            };
        case CHANGE_CURRENT_PAGE:
            return {
                ...state, currentPage: action.page
            };
        case TOGGLE_IS_FETCHING:
            return {
                ...state, isFetching: action.isFetching
            };
        case FOLLOWING_IN_PROGRESS:
            return {
                ...state,
                followingInProgress: action.isFetching ?
                    [...state.followingInProgress, action.userId] :
                    state.followingInProgress.filter(id => id != action.userId)
            };
        default:
            return state

    }

};


export const showNewUsers = (users) => ({type: SHOW_NEW_USERS, users});
export const userFollow = (id) => ({type: USER_FOLLOW, id});
export const userUnfollowing = (id) => ({type: USER_UNFOLLOWING, id});
export const allUserCount = (userCount) => ({type: ALL_USER_COUNT, userCount});
export const changeCurrentPage = (page) => ({type: CHANGE_CURRENT_PAGE, page});
export const toggleIsFetching = (isFetching) => ({type: TOGGLE_IS_FETCHING, isFetching});
export const followingInProgress = (isFetching, userId) => ({type: FOLLOWING_IN_PROGRESS, isFetching, userId});


export const getUsers = (currentPage, maxUsersOnPage) => (dispatch) => {
        dispatch(toggleIsFetching(true));
        dispatch(changeCurrentPage(currentPage));
        usersAPI.getUsers(currentPage, maxUsersOnPage)
            .then(data => {
                dispatch(toggleIsFetching(false));
                dispatch(allUserCount(data.totalCount));
                dispatch(showNewUsers(data.items));

            })
    };
export const follow = (id) => (dispatch) => {
    dispatch(followingInProgress(true,id));
    usersAPI.followUser(id)
        .then(data => {
            if (data.resultCode === 0) {
                dispatch(userFollow(id));
            }
            dispatch(followingInProgress(false, id));
        })
};
export const unfollow = (id) => (dispatch) => {
    dispatch(followingInProgress(true,id));
    usersAPI.unfollowUser(id)
        .then(data => {
            if (data.resultCode === 0) {
                dispatch(userUnfollowing(id));
            }
            dispatch(followingInProgress(false, id));
        })
};


export default FindUsersReducer