import {profileAPI} from "../API/API";


const NEW_POST = 'NEW-POST';
const LOAD_PROFILE = 'LOAD-PROFILE';
const PROFILE_TOGGLE_FETCHING = 'PROFILE-TOGGLE-FETCHING';
const SET_STATUS = 'SET-STATUS';

let initialPost = {
    posts: [
        {id: 1, message: 'Xaydi Xo', like: 23},
        {id: 2, message: 'Kavo', like: 34},
        {id: 3, message: 'oldmen', like: 55}
    ],
    currentProfile: null,
    isFetching: true,
    profileStatus:""
};

const profileReducer = (state = initialPost, action) => {

    switch ( action.type ) {

        case NEW_POST:
            return {
                ...state,
                posts: [{id: 5, message: action.text, like: 0}, ...state.posts],
                newPostText: ''
            };
        case LOAD_PROFILE:
            return {
                ...state, currentProfile: action.profile
            };
        case PROFILE_TOGGLE_FETCHING:
            return {
                ...state, isFetching: action.isFetching
            };
        case SET_STATUS:
            return {
                ...state,profileStatus:action.status
            };
        default:
            return state;
    }
};



export const addNewPost = (text) => ({type: NEW_POST,text});
export const loadProfile = (profile) => ({type: LOAD_PROFILE, profile});
export const profileToggleFetching = (isFetching) => ({type: PROFILE_TOGGLE_FETCHING, isFetching});
export const setStatus = (status) => ({type:SET_STATUS,status});


export const profileLoader = (id) => (dispatch) => {

    dispatch(profileToggleFetching(true));
    profileAPI.getProfile(id)
        .then(data => {debugger
            dispatch(loadProfile(data));
            dispatch(profileToggleFetching(false));
        })
};
export const profileStatusLoader = (id) => (dispatch) => {
    profileAPI.getStatus(id).then(response => {
        dispatch(setStatus(response.data));
    })
};
export const profileStatusUpdate = (status) => (dispatch) => {
    profileAPI.setStatus(status).then(response => {
        if (response.data.resultCode === 0) {
        dispatch(setStatus(status));
    }})
};

export default profileReducer;

