import React from 'react'


export class ProfileStatus extends React.Component {

    state = {
        editMode:false,
        status:this.props.profileStatus

    };

    componentDidUpdate(prevProps, prevState) {

        if(prevProps.profileStatus !== this.props.profileStatus) {
            this.setState({status:this.props.profileStatus})
        }
    }

    activateEditMode = () => {
        this.setState({editMode:true})
    };
    changeLocalStatus = (e) => {
        this.setState({status:e.currentTarget.value})
    };
    updateStatus = (e) => {
        this.setState({editMode:false});
        this.props.profileStatusUpdate(this.state.status)
    };


    render() {
        console.log('render');
        return (
            <div>
                {!this.state.editMode &&
                <div><span onClick={this.activateEditMode}>{this.props.profileStatus || 'Введите статус...'}</span></div>}

                {this.state.editMode &&
                <div><input onChange={this.changeLocalStatus} autoFocus={true} onBlur={this.updateStatus} value={this.state.status} type="text"/></div>}
            </div>
        )
    }
}